import PropTypes from 'prop-types';
import { Navigate, useLocation } from 'react-router-dom';
import commonUtils from '../utils';

function Authenticate({ children, nextPath }) {
  const authURL = process.env.REACT_APP_AUTH_URL;
  const location = useLocation();
  const cookieValue = commonUtils.getUserInfo();
  const isLoggedIn = cookieValue !== '';

  if (isLoggedIn === true) {
    return children;
  }

  //  const path = `/login?next=${location.pathname}${location.search}`;
  //  return <Navigate to={path} replace />;
  if (nextPath) {
    return <Navigate to={nextPath} replace />;
  }
  const path = `https://${authURL}/oauth2/authorize?next=${location.pathname}${location.search}`;
  window.location.replace(path);
  return null;
}

Authenticate.defaultProps = {
  nextPath: '',
};

Authenticate.propTypes = {
  children: PropTypes.node.isRequired,
  nextPath: PropTypes.string,
};

export default Authenticate;
